
.comics {

    &__header {
        background-color: rgba(35, 34, 34, 1);
        height: 100px;
        padding: 0 25px 0 45px;
        position: relative;
        display: flex;
        justify-content: space-between;

        .descr {
            color: white;
            position: absolute;
            top: 18px;
            left: 280px;
            line-height: 28px;
            font-weight: 721;
            font-size: 24px;
        }
    }


     &__list {
        margin-top: 45px;
    }
    &__grid {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        row-gap: 55px;
    }
    &__item {
        transition: 0.3s transform;
        &-img {
            box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
            width: 225px;
            height: 345px;
        }
        &-name {
            margin-top: 10px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
        }
        &-price {
            margin-top: 5px;
            font-weight: bold;
            font-size: 14px;
            line-height: 18px;
            color: rgba(0, 0, 0, 0.6);
            text-transform: uppercase;
        }
        &:hover {
            transform: translateY(-5px);
        }
    }
}